            人们的生理和心理随时都有可能发生老化和病变;健康管理是一种对个人及人群的健康危险因素进行全面管理的过程，提供科学的健康指导、健康生活方式的干预，调动其自觉性和主动性，有效地利用有限的资源来达到最大的健康改善效果，
