import Page404 from '../views/404.vue'
//登录组件
import login from '../views/Login/login.vue';
//轮播
import Pic from '../views/Pic'
//首页 
import index from '../views/Index';
//个人中心
import person from '../views/Setting/person.vue'
 const staticRoute = [
    {
        path:"/",
        redirect:login
    },
    //这个路由是登录路由 
    {
        path: '/login',
        name: 'login',
        component: login
    },
    {
        path:"/404",
        name:"Page404",
        component:Page404
    },
    {
        path: '/index',
        name: 'index',
        component: index,
        //路由元信息
        meta:{
          isLogin:true
        },
        children:[
          //首页
          {
            path: '/Pic',
            name: 'Pic',
            component: Pic
          },
          //个人中心
          {
            path: '/person',
            name: 'person',
            component: person
          },
         
    ]},
    
]

export default staticRoute